import { Group, ObjectLoader } from "three";
import * as THREE from "three";
import * as TWEEN from "@tweenjs/tween.js";
import Bird from "./Bird";
import State from "../../state"

export default class Birds extends Group {
  constructor() {
    super();

    this.name = "birds";
    this._maxBirds = 10;
    this._birds = [];
    for (let i = 0; i < this._maxBirds; i++) {
      const bird = new Bird();
      this.randomizeStart(bird);
      bird.position.index = i;
      this.add(bird);
      this._birds.push(bird);
    }

    document.addEventListener("keydown", (e) => {
      if (e.code == "KeyB") {
        let groupFinished = false;
        for (let i = 0; i < this._maxBirds; i++) {
          this.randomizeStart(this._birds[i]);
          this._birds[i].children[0].material.opacity = 1;
          this._birds[i].children[1].material.opacity = 1;
          new TWEEN.Tween(this._birds[i].position)
            .to(
              {
                x: THREE.Math.randFloat(-2, 2),
                y: THREE.Math.randFloat(2, 5),
                z: THREE.Math.randFloat(-2, 2),
              },
              THREE.Math.randInt(10000, 12000)
            )
            .onComplete((target) => {
              this._birds[target.index].children[0].material.opacity = 0;
              this._birds[target.index].children[1].material.opacity = 0;
              if(!groupFinished) State.pushEvent("birdPerch");
              groupFinished = true;
            })
            .easing(TWEEN.Easing.Quadratic.Out)
            .start();
        }
      }
    });
  }

  randomizeStart(bird) {
    let pNoise = 2;
    let rNoise = 0.5;
    bird.rotation.x = THREE.Math.randFloat(-rNoise, rNoise);
    bird.position.x = 15 + THREE.Math.randFloat(-pNoise * 2, pNoise * 2);
    bird.position.y = 10 + THREE.Math.randFloat(-pNoise, pNoise);
    bird.position.z = THREE.Math.randFloat(-pNoise, pNoise);
    // bird.rotation.y = THREE.Math.randFloat(-rNoise, rNoise);
    bird.rotation.z = THREE.Math.randFloat(0, rNoise);
  }
}
