import { Group, ShapeGeometry, MeshPhongMaterial, Mesh, MeshStandardMaterial } from "three";
import * as THREE from "three";
import * as TWEEN from "@tweenjs/tween.js";

export default class Bird extends Group {
  constructor() {
    super();

    this.name = "birds";

    let size = 0.1;
    let wingSpeed = 100;
    const wing = new THREE.Shape([
      new THREE.Vector3(-size, 0, 0),
      new THREE.Vector3(size, 0, 0),
      new THREE.Vector3(0, size * 3, 0),
    ]);

    const geometry = new ShapeGeometry(wing);
    const material = new MeshStandardMaterial({
      color: 0x00acee,
      side: THREE.DoubleSide,
      transparent: true,
    });
    const material2 = new MeshStandardMaterial({
      color: 0x00acee,
      side: THREE.DoubleSide,
      transparent: true,
    });
    const wing1 = new Mesh(geometry, material);
    const wing2 = new Mesh(geometry, material2);
    wing1.rotation.x = 1;
    wing2.rotation.x = 1;
    this.add(wing1, wing2);

    new TWEEN.Tween(wing1.rotation)
      .to({ x: THREE.Math.randFloat(1.5, 2.5) }, THREE.Math.randInt(wingSpeed, wingSpeed * 2))
      .onUpdate((target) => {
        wing2.rotation.x = -wing1.rotation.x;
      })
      .easing(TWEEN.Easing.Quadratic.Out)
      .yoyo(true)
      .repeat(Infinity)
      .start();
  }
}
