/**
 * entry.js
 *
 * This is the first file loaded. It sets up the Renderer,
 * Scene and Camera. It also starts the render loop and
 * handles window resizes.
 *
 */

import {
  WebGLRenderer,
  PerspectiveCamera,
  Scene,
  Vector3,
  Fog,
  PCFSoftShadowMap,
  Color,
} from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import Stats from "three/examples/js/libs/stats.min.js";
import SeedScene from "./objects/Scene.js";
import BasicLights from "./objects/Lights.js";
import * as TWEEN from "@tweenjs/tween.js";
import State from "./state";
import Text from "./objects/Text/Text";
import Tweets from "./objects/Tweets/Tweets.js";

const scene = new Scene();
const camera = new PerspectiveCamera();
const renderer = new WebGLRenderer({ antialias: true, alpha: true });
const controls = new OrbitControls(camera, renderer.domElement);
const seedScene = new SeedScene();
const stats = new Stats();
// scene
scene.fog = new Fog(0xf2e6d9, 8, 20);
// scene.background = new Color(0xf2e6d9);
scene.add(seedScene);
scene.add(new BasicLights());

// camera
if (!State.intro) {
  // camera.position.set(5, 3, 8);
  camera.position.set(State.cameraStart.x,State.cameraStart.y,State.cameraStart.z);
  camera.lookAt(new Vector3(0, 2, 0));
  controls.target = new Vector3(0, 2, 0);
  controls.update();
  controls.saveState();
} else {
  camera.position.set(24, 14, -5);
  controls.target = new Vector3(0, 2, 7);
  camera.lookAt(controls.target);
  controls.update();
  controls.saveState();
}

// renderer
renderer.setPixelRatio(window.devicePixelRatio);
// renderer.setClearColor(0xf2e6d9, 1);
renderer.autoClear = false;
renderer.setClearColor(0x000000, 0.0);

// shadow
renderer.shadowMap.enabled = true;
renderer.shadowMap.type = PCFSoftShadowMap; // default THREE.PCFSoftShadowMap

// render loop
const onAnimationFrameHandler = (timeStamp) => {
  stats.begin();
  renderer.render(scene, camera);
  stats.end();
  seedScene.update && seedScene.update(timeStamp);
  window.requestAnimationFrame(onAnimationFrameHandler);
  TWEEN.update(timeStamp);
};
window.requestAnimationFrame(onAnimationFrameHandler);

// resize
const windowResizeHanlder = () => {
  const { innerHeight, innerWidth } = window;
  renderer.setSize(innerWidth, innerHeight);
  camera.aspect = innerWidth / innerHeight;
  camera.updateProjectionMatrix();
};
windowResizeHanlder();
window.addEventListener("resize", windowResizeHanlder);

// keyboard
document.addEventListener("keydown", (e) => {
  if (e.code == "KeyR") {
    controls.reset();
  } else if (e.code == "KeyS") {
    new TWEEN.Tween(camera.position)
      .to(State.cameraStart, 10000)
      .easing(TWEEN.Easing.Sinusoidal.InOut)
      .start();
    new TWEEN.Tween(controls.target)
      .to(new Vector3(0, 2, 0), 10000)
      .easing(TWEEN.Easing.Sinusoidal.InOut)
      .onUpdate((obj) => {
        camera.lookAt(obj);
      })
      .onComplete(() => {
        controls.target = new Vector3(0, 2, 0);
        controls.update();
        controls.saveState();
        State.rotateScene = true;
        State.pushEvent("endIntro");
      })
      .start();
  }
});

// dom
document.body.appendChild(renderer.domElement);
// document.body.appendChild(stats.domElement);


if (State.liveData) {
  document.getElementById("lblData").textContent = "Retrieving latest compliance data...";
  fetch("https://nufx-scraper-dynamic.azurewebsites.net/api/scraper")
    .then((response) => response.json())
    .then((data) => {
      document.getElementById("lblData").textContent = "Live data";
      State.data = data.data;
      seedScene.add(new Text());
    })
    .catch((error) => {
      document.getElementById("lblData").textContent = "data error";
    });
} else {
  seedScene.add(new Text());
}
