import { Group } from "three";
import Land from "./Land/Land.js";
import Leaves from "./Leaves/Leaves.js";
import Tweets from "./Tweets/Tweets.js";
import Birds from "./Birds/Birds.js";
import State from "../state";

export default class SeedScene extends Group {
  constructor() {
    super();

    this._land = new Land();
    this._leaves = new Leaves();
    this._birds = new Birds();

    this.add(this._land, this._leaves, this._birds, new Tweets());
  }

  update(timeStamp) {
    if (State.rotateScene) this.rotation.y += 0.0005;
  }
}
