import {
  Group,
  FontLoader,
  TextGeometry,
  Mesh,
  MeshPhongMaterial,
  BufferGeometry,
} from "three";
import * as TWEEN from "@tweenjs/tween.js";
import * as THREE from "three";
import State from "../../state";

export default class Text extends Group {
  constructor() {
    super();

    this._currentObj = null;
    this._counter = 2534;
    const loader = new FontLoader();
    loader.load("helvetiker_bold.typeface.json", (font) => {
      State.font = font;
      let lblDate = this.createText(
        "DATE",
        0.3,
        0xf2e6d9,
        font,
        [-5, 5.2, -15]
      );
      this.add(lblDate);

      let valDate = this.createText(
        (new Date(State.data[0].date)).toDateString(),
        1,
        0xf2e6d9,
        font,
        [-5, 4, -15]
      );
      this.add(valDate);


      let lblPositiveRate = this.createText(
        "POSITIVE RATE",
        0.3,
        0xf2e6d9,
        font,
        [-10, 5.2, -15]
      );
      this.add(lblPositiveRate);

      let valPositiveRate = this.createText(
        State.data[0].rate,
        1,
        0xf2e6d9,
        font,
        [-10, 4, -15]
      );
      this.add(valPositiveRate);

      let valProtect = this.createText("#PROTECTTHEPACK", 3, 0xff0000, font);
      this.add(valProtect);
      valProtect.position.x = -15;
      valProtect.position.z = -15;

      let lblCompleted = this.createText(
        "TESTS COMPLETED",
        0.3,
        0xf2e6d9,
        font,
        [-15, 5.2, -15]
      );
      this.add(lblCompleted);

      this.updateText("", font);
    });
  }

  createText(text, size, color, font, pos) {
    const geometry = new TextGeometry(text, {
      font: font,
      size: size,
      height: 0.04,
    });

    let textMesh1 = new Mesh(
      new BufferGeometry().fromGeometry(geometry),
      new MeshPhongMaterial({ color: color })
    );
    if (pos) {
      textMesh1.position.x = pos[0];
      textMesh1.position.y = pos[1];
      textMesh1.position.z = pos[2];
    }
    return textMesh1;
  }

  updateText(text, font) {
    this._counter += THREE.Math.randInt(5, 50);
    const geometry = new TextGeometry(this._counter.toLocaleString(), {
      font: font,
      size: 1,
      height: 0.04,
    });

    let textMesh1 = new Mesh(
      new BufferGeometry().fromGeometry(geometry),
      new MeshPhongMaterial({ color: 0xf2e6d9, flatShading: true, opacity: 1 })
    );
    textMesh1.position.x = -15;
    textMesh1.position.z = -15;
    textMesh1.position.y = 4;
    this.add(textMesh1);
    if (this._currentObj) this.remove(this._currentObj);
    this._currentObj = textMesh1;

    if (this._counter < 7000) {
      setTimeout(() => {
        this.updateText(text, font);
      }, THREE.Math.randInt(1000, 3000));
    }
  }
}
