import * as THREE from "three";
import {
  Group,
  FontLoader,
  TextGeometry,
  Mesh,
  MeshPhongMaterial,
  BufferGeometry,
} from "three";
import State from "../../state";
import * as signalR from "@microsoft/signalr";
import * as TWEEN from "@tweenjs/tween.js";

export default class Tweets extends THREE.Group {
  constructor(...args) {
    super(...args);

    this.renderTweet();

    // const curve = new THREE.EllipseCurve(
    //   0,
    //   0, // ax, aY
    //   6,
    //   6, // xRadius, yRadius
    //   0,
    //   Math.PI / 4, // aStartAngle, aEndAngle
    //   false, // aClockwise
    //   0 // aRotation
    // );

    // const points = curve.getPoints(10);
    // const geometry = new THREE.BufferGeometry().setFromPoints(points);
    // const material = new THREE.LineBasicMaterial({ color: 0xe2c9af, transparent:true, opacity: 0.5 });
    // const ellipse = new THREE.Line(geometry, material);
    // this.add(ellipse);
    // ellipse.position.y = 1;
    // ellipse.rotation.x = Math.PI / 2;

    // new TWEEN.Tween(ellipse.rotation)
    //   .to({ z: Math.PI * 2 }, THREE.Math.randInt(2000, 4000))
    //   .easing(TWEEN.Easing.Linear.None)
    //   .onUpdate((target) => {})
    //   .onComplete((target) => {})
    //   .repeat(Infinity)
    //   .start();

    State.addSubscriber(this);

    const shape = new THREE.Shape();
    shape.moveTo(0, 0);
    shape.lineTo(0, 2);
    shape.lineTo(4, 2);
    shape.lineTo(4, 0);
    shape.lineTo(1, 0);
    shape.lineTo(0.3, -0.5);
    shape.lineTo(0.4, 0);
    shape.lineTo(0, 0);

    const extrudeSettings = {
      depth: 0.2,
      bevelEnabled: false,
    };

    const geometry = new THREE.ExtrudeBufferGeometry(shape, extrudeSettings);

    this._isTweetActive = false;
    //const geometry = new THREE.PlaneGeometry(4.5, 2, 1, 1);
    const material = new THREE.MeshPhongMaterial({
      color: 0xffffff,
      emissive: 0xffffff,
      specular: 0xffffff,
      side: THREE.DoubleSide,
    });
    const plane = new THREE.Mesh(geometry, material);
    plane.position.set(3.6, -1.3, -0.2);
    this.add(plane);

    this.scale.set(0.01, 0.01, 0.01);

    this._header = false;
    this._currentObj = null;

    this.loadData();

    if (State.showDefaultTweet) {
      setTimeout(() => {
        this.renderTweet("Just got done with my first test at Cabot!");
      }, 2500);
    }
  }

  renderTweet(text) {
    if (this._isTweetActive) return;
    if (State.font == null) return;

    if (!this._header) {
      var header = this.createWrappedText({
        string: "#ProtectThePack",
        font: State.font,
        size: 0.1,
        color: 0x00acee,
        lineLength: 24,
        height: 0.01,
        coords: {
          x: 4,
          y: 0.2,
          z: 0,
        },
      });
      this.add(header);

      var footer = this.createWrappedText({
        string: new Date().toLocaleTimeString(),
        font: State.font,
        size: 0.1,
        color: 0xefefef,
        lineLength: 24,
        height: 0.01,
        coords: {
          x: 4,
          y: -1,
          z: 0,
        },
      });
      this.add(footer);
      this._header = true;
    }

    var textMesh1 = this.createWrappedText({
      string: '"' + text + '"',
      font: State.font,
      size: 0.2,
      color: 0x333333,
      lineLength: 24,
      height: 0.01,
      coords: {
        x: 4,
        y: -0.1,
        z: 0,
      },
    });

    this.add(textMesh1);
    if (this._currentObj) this.remove(this._currentObj);
    this._currentObj = textMesh1;

    this.position.y = 3;
    this.rotation.y = Math.PI / 2;
    this.scale.set(0.01, 0.01, 0.01);
    this._opacity = { opacity: 1 };
    this.traverse((node) => {
      if (node.material) {
        node.material.opacity = 1;
        node.material.transparent = true;
      }
    });

    new TWEEN.Tween(this.rotation)
      .to({ y: 0 }, 2500)
      .easing(TWEEN.Easing.Quartic.Out)
      .onComplete(() => {

        State.pushEvent("onTweet");
        new TWEEN.Tween(this._opacity)
          .to({ opacity: 0 }, 1500)
          .delay(6000)
          .easing(TWEEN.Easing.Quadratic.Out)
          .onUpdate(() => {
            this.traverse((node) => {
              if (node.material) {
                node.material.opacity = this._opacity.opacity;
              }
            });
          })
          .onComplete(() => {
            this._isTweetActive = false;
          })
          .start();
      })
      .start();
    new TWEEN.Tween(this.scale)
      .to({ x: 1, y: 1, z: 1 }, 2500)
      .easing(TWEEN.Easing.Quadratic.Out)
      .start();
  }

  loadData() {
    const connection = new signalR.HubConnectionBuilder()
      .withUrl(`https://nufx-scraper.azurewebsites.net/api`)
      .configureLogging(signalR.LogLevel.Information)
      .build();

    connection.on("newMessage", (message) => {
      this.renderTweet(message.text);
      console.log(message);
    });
    connection.onclose(() => console.log("disconnected"));

    console.log("connecting...");
    connection
      .start()
      .then(() => {
        State.signalRConnected = true;
        document.getElementById("lblSignalR").textContent = "Connected";
      })
      .catch((error) => {
        document.getElementById("lblSignalR").textContent = "network error";
      });
  }

  createWrappedText(options) {
    var group = new THREE.Object3D();

    // Save Starting Positions
    var position = {
      x: options.coords.x,
      y: options.coords.y,
      z: options.coords.z,
    };

    var cutText = options.string;

    while (cutText.length > options.lineLength) {
      // Grab First part of string and render as new line.
      // Make sure string is cut at a space and not mid-word.
      if (cutText.charAt(options.lineLength) === " ") {
        // cut line at space
        var line = cutText.substring(0, options.lineLength);
        cutText = cutText.slice(options.lineLength);
      } else {
        // search for closest space
        var counter = 1;
        while (cutText.charAt(options.lineLength - counter) !== " ") {
          counter++;
        }

        // cut line at space
        var line = cutText.substring(0, options.lineLength - counter);
        cutText = cutText.slice(options.lineLength - counter);
      }

      line = line.trim();

      // Create line geometry, material, and mesh
      var textGeometry = new THREE.TextGeometry(line, {
          size: options.size,
          height: options.height,
          font: options.font,
        }),
        textMaterial = new THREE.MeshLambertMaterial({ color: options.color }),
        textMesh = new THREE.Mesh(textGeometry, textMaterial);

      // Set it's position, then offset y coord for next line
      textMesh.position.set(position.x, position.y, position.z);
      group.add(textMesh);
      position.y -= options.size * 1.5;
    }

    cutText = cutText.trim();
    // If text is less than lineLength, just render one text geometry
    var textGeometry = new THREE.TextGeometry(cutText, {
        size: options.size,
        height: options.height,
        font: State.font,
      }),
      textMaterial = new THREE.MeshLambertMaterial({ color: options.color }),
      textMesh = new THREE.Mesh(textGeometry, textMaterial);

    textMesh.position.set(position.x, position.y, position.z);
    group.add(textMesh);

    return group;
  }
}
