import {
  Group,
  Shape,
  PlaneGeometry,
  MeshPhongMaterial,
  Mesh,
  ShapeGeometry,
  DoubleSide,
} from "three";
import * as THREE from "three";
import * as TWEEN from "@tweenjs/tween.js";
import State from "../../state";

export default class Leaves extends Group {
  constructor() {
    super();
    State.addSubscriber(this);
    let leafSize = 0.1;
    const triangleShape = new THREE.Shape([
      new THREE.Vector3(leafSize, 0, 0),
      new THREE.Vector3(0, leafSize * 3, 0),
      new THREE.Vector3(-leafSize, 0, 0),
      new THREE.Vector3(0, -leafSize * 2, 0),
    ]);

    this.name = "leaves";
    this._leaves = [];
    this._maxLeaves = 1000;
    for (let i = 0; i < this._maxLeaves; i++) {
      const geometry = new ShapeGeometry(triangleShape); //new PlaneGeometry(0.3, 0.4);
      const material = new MeshPhongMaterial({
        color: 0x78eb8a,
        side: THREE.DoubleSide,
        transparent: true,
      });
      const leaf = new Mesh(geometry, material);
      leaf.castShadow = true;
      this._leaves.push(leaf);
      this.randomize1(leaf);
      this.add(leaf);
    }

    document.addEventListener("keydown", (e) => {
      if (e.code == "KeyN" || e.code == "KeyM" || e.code == "KeyX") {
        let leavesToDrop =
          e.code == "KeyM" ? 100 : e.code == "KeyX" ? this._maxLeaves : 10;

        State.pushEvent("leafFall");
        for (let i = 0; i < leavesToDrop; i++) {
          let index =
            e.code == "KeyX" ? i : THREE.Math.randInt(0, this._maxLeaves);
          if (!this._leaves[index].fallen) {
            this._leaves[index].position.index = index;
            this._leaves[index].isFallen = true;
            new TWEEN.Tween(this._leaves[index].position)
              .to({ y: 0 }, THREE.Math.randInt(2000, 4000))
              .delay(THREE.Math.randInt(0, 1500))
              .easing(TWEEN.Easing.Back.InOut)
              .onUpdate((target) => {
                this._leaves[target.index].rotation.x += THREE.Math.randFloat(
                  0,
                  0.1
                );
                this._leaves[target.index].rotation.y += THREE.Math.randFloat(
                  0,
                  0.1
                );
                this._leaves[target.index].rotation.z += THREE.Math.randFloat(
                  0,
                  0.1
                );
              })
              .onComplete((target) => {
                this._leaves[target.index].material.opacity = 0.8;
              })
              .start();
          }
        }
      } else {
        if (e.code == "KeyL") {
          this.randomizeN(100);
          State.pushEvent("leafGrow");
        }
      }
    });
  }

  randomize1(leaf) {
    let y = THREE.Math.randFloat(0, 3.5);
    let w = 1.8 + Math.abs(3.5 - y) / 4;
    leaf.position.x = THREE.Math.randFloat(-w, w);
    leaf.position.z = THREE.Math.randFloat(-w, w);
    leaf.position.y = 1.8 + y;
    leaf.rotation.x += THREE.Math.randFloat(0, 20);
    leaf.rotation.y += THREE.Math.randFloat(0, 20);
    leaf.rotation.z += THREE.Math.randFloat(0, 20);
    leaf.material.opacity = 1;
    leaf.scale.x = 0.2;
    leaf.scale.y = 0.2;
    leaf.scale.z = 0.2;
    leaf.isFallen = false;
    new TWEEN.Tween(leaf.scale)
      .to({ x: 1, y: 1, z: 1 }, THREE.Math.randInt(500, 1000))
      .delay(THREE.Math.randInt(0, 1000))
      .easing(TWEEN.Easing.Quadratic.Out)
      .start();
  }

  randomizeN(number) {
    this._leaves
      .filter((x) => x.isFallen == true)
      .slice(0, number)
      .forEach((x) => this.randomize1(x));
  }

  onEvent(evt) {
    if (evt == "birdPerch") {
      this.randomizeN(100);
      State.pushEvent("leafGrow");
    }
  }
}
